import React, { useEffect, useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import { Col, Row, Button, Label, Input, Table } from "reactstrap";
import { useParams, useHistory } from "react-router-dom";
import Loading from "components/common/loading/Loading";
import adminQLCTDTApi from "api/admin/adminQLCTDTApi";
import "./AdminQLCTDT.css";
import i18n from "i18n/i18n";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";

const AdminQLCTDT = (props) => {
    const { t } = useTranslation();
    let { id } = useParams();
    let history = useHistory();
    const isCancelled = useRef(false);
    const [isEditing1, setIsEditing1] = useState(false);
    const [isEditing2, setIsEditing2] = useState(false);
    const [isEditing3, setIsEditing3] = useState(false);
    const [isEditing4, setIsEditing4] = useState(false);
    const [isEditing5, setIsEditing5] = useState(false);
    const [isEditing6, setIsEditing6] = useState(false);
    const [isEditing7, setIsEditing7] = useState(false);
    const [isEditing8, setIsEditing8] = useState(false);
    const [ctdt, setCtdt] = useState({
        id: "",
        maCTDT: "",
        loaiHinh: "",
        ten: "",
        trinhDo: "",
        // TOTAL_CREDIT: 0,
        doiTuong: "",
        quiTrinhDaoTao: "",
        GRADUATION_CONDITIONS: "",
    });
    const [danhSachNganhDaoTao, setDanhSachNganhDaoTao] = useState({
        contents: [],
        page: 0,
        total: 0,
    });
    const [isRefresh, setIsRefresh] = useState(false);
    useEffect(() => {
        (async () => {
            try {
                const ctdt = await adminQLCTDTApi.getCTDT(id);
                if (!isCancelled.current) {
                    setCtdt(ctdt);
                }
                const response = await adminQLCTDTApi.getNganhDaoTaoTuCTDT(id);
                if (!isCancelled.current) {
                    setDanhSachNganhDaoTao(response);
                }
            } catch (err) {
                if (!isCancelled.current) {
                    setDanhSachNganhDaoTao({
                        contents: [],
                        page: 0,
                        total: 0,
                    });
                }
            }
        })();
        return () => {
            isCancelled.current = true;
        };
    }, [isRefresh]);

    const handleEditButton1 = () => {
        setIsEditing1(true);
    };
    const handleEditButton2 = () => {
        setIsEditing2(true);
    };
    const handleEditButton3 = () => {
        setIsEditing3(true);
    };
    const handleEditButton4 = () => {
        setIsEditing4(true);
    };
    const handleEditButton5 = () => {
        setIsEditing5(true);
    };
    const handleEditButton6 = () => {
        setIsEditing6(true);
    };
    const handleEditButton7 = () => {
        setIsEditing7(true);
    };
    const handleEditButton8 = () => {
        setIsEditing8(true);
    };
    const handleSaveButton1 = () => {
        setIsEditing1(false);
    };
    const handleSaveButton2 = () => {
        setIsEditing2(false);
    };
    const handleSaveButton3 = () => {
        setIsEditing3(false);
    };
    const handleSaveButton4 = () => {
        setIsEditing4(false);
    };
    const handleSaveButton5 = () => {
        setIsEditing5(false);
    };
    const handleSaveButton6 = () => {
        setIsEditing6(false);
    };
    const handleSaveButton7 = () => {
        setIsEditing7(false);
    };
    const handleSaveButton8 = () => {
        setIsEditing8(false);
    };
    const handleSaveAllButton = async () => {
        if (ctdt.id !== "") {
            const data = (({
                maCTDT,
                loaiHinh,
                ten,
                trinhDo,
                // tongTinChi,
                doiTuong,
                quiTrinhDaoTao,
                dieuKienTotNghiep,
            }) => ({
                maCTDT,
                loaiHinh,
                ten,
                trinhDo,
                // tongTinChi: Number(tongTinChi),
                doiTuong,
                quiTrinhDaoTao,
                dieuKienTotNghiep,
            }))(ctdt);
            try {
                const response = await adminQLCTDTApi.updateCTDT(ctdt.id, data);
                if (
                    response.message === "UPDATE_CHUONGTRINHDAOTAO_SUCCESSFULLY"
                ) {
                    // do something
                    toast.success(
                        i18n.t(`quanlychuongtrinhdaotao.${response.message}`)
                    );
                    setIsRefresh(!isRefresh);
                } else {
                    // do something
                    toast.error(
                        i18n.t(
                            `quanlychuongtrinhdaotao.UPDATE_CHUONGTRINHDAOTAO_FAILED`
                        )
                    );
                }
            } catch (err) {
                if (err?.response?.data?.error?.includes("ER_DUP_ENTRY")) {
                    toast.error(t("quanlychuongtrinhdaotao.ER_DUP_ENTRY"));
                } else {
                    toast.error(
                        i18n.t(
                            `quanlychuongtrinhdaotao.UPDATE_CHUONGTRINHDAOTAO_FAILED`
                        )
                    );
                }
            }
        }
    };
    return (
        <>
            {ctdt.id === "" ? (
                <Loading />
            ) : (
                <div className="ManageProgramEducationBasic__container">
                    <Row className="ManageProgramEducation_background ManageProgramEducation__tools">
                        <Button
                            outline
                            className="btn"
                            color="success"
                            onClick={handleSaveAllButton}
                        >
                            {t("SAVE")}
                        </Button>
                        {/* <Button outline className="btn" color="danger">
                            {t("ROLLBACK")}
                        </Button> */}
                    </Row>

                    <Row className="ManageProgramEducation_background ManageProgramEducation__contents">
                        <Col>
                            <h4 className="item-label">
                                {t("PROGRAM_LIST").toUpperCase()}
                            </h4>
                            <Row className="mt-4">
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("PROGRAM_CODE")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        name="PROGRAM_CODE"
                                        id="PROGRAM_CODE"
                                        placeholder=""
                                        defaultValue={ctdt?.maCTDT || ""}
                                        disabled={!isEditing1}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                maCTDT: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    <div>
                                        {isEditing1 === false ? (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleEditButton1}
                                            >
                                                {t("CHANGE")}
                                            </a>
                                        ) : (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleSaveButton1}
                                            >
                                                {t("SAVE")}
                                            </a>
                                        )}
                                    </div>
                                </Col>
                            </Row>

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("TYPE_EDUCATION")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        className="input"
                                        name="typeEducation"
                                        id="typeEducation"
                                        placeholder={`${t(
                                            "ENTER_TYPE_EDUCATION"
                                        )}`}
                                        defaultValue={ctdt?.loaiHinh || ""}
                                        disabled={!isEditing2}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                loaiHinh: e?.target?.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    <div>
                                        {isEditing2 === false ? (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleEditButton2}
                                            >
                                                {t("CHANGE")}
                                            </a>
                                        ) : (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleSaveButton2}
                                            >
                                                {t("SAVE")}
                                            </a>
                                        )}
                                    </div>
                                </Col>
                            </Row>

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("PROGRAM_NAME")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        className="input"
                                        name="name"
                                        id="name"
                                        placeholder={`${t("ENTER_PROGRAM_NAME")}`}
                                        defaultValue={ctdt?.ten || ""}
                                        disabled={!isEditing3}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                ten: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    {isEditing3 === false ? (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleEditButton3}
                                        >
                                            {t("CHANGE")}
                                        </a>
                                    ) : (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleSaveButton3}
                                        >
                                            {t("SAVE")}
                                        </a>
                                    )}
                                </Col>
                            </Row>

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("UNIVERSITY_DEGREE")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        className="universityDegree"
                                        name="selectDegree"
                                        id="universityDegree"
                                        placeholder={t(
                                            "ENTER_UNIVERSITY_DEGREE"
                                        )}
                                        defaultValue={ctdt?.trinhDo || ""}
                                        disabled={!isEditing4}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                trinhDo: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    {isEditing4 === false ? (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleEditButton4}
                                        >
                                            {t("CHANGE")}
                                        </a>
                                    ) : (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleSaveButton4}
                                        >
                                            {t("SAVE")}
                                        </a>
                                    )}
                                </Col>
                            </Row>

                            {/* <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("TOTAL_CREDIT")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        className="input"
                                        name="TOTAL_CREDIT"
                                        id="TOTAL_CREDIT"
                                        placeholder=""
                                        defaultValue={ctdt?.tongTinChi || ""}
                                        disabled={!isEditing5}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                tongTinChi: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    {isEditing5 === false ? (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleEditButton5}
                                        >
                                            {t("CHANGE")}
                                        </a>
                                    ) : (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleSaveButton5}
                                        >
                                            {t("SAVE")}
                                        </a>
                                    )}
                                </Col>
                            </Row> */}

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("AUDIENCE")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        className="input"
                                        name="doituong"
                                        id="doituong"
                                        placeholder=""
                                        defaultValue={ctdt?.doiTuong || ""}
                                        disabled={!isEditing6}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                doiTuong: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    {isEditing6 === false ? (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleEditButton6}
                                        >
                                            {t("CHANGE")}
                                        </a>
                                    ) : (
                                        <a
                                            className="typeLink"
                                            type="button"
                                            onClick={handleSaveButton6}
                                        >
                                            {t("SAVE")}
                                        </a>
                                    )}
                                </Col>
                            </Row>

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("TRAINING_PROCESS")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        style={{height: "250px"}}
                                        type="textarea" 
                                        name="TRAINING_PROCESS"
                                        id="TRAINING_PROCESS"
                                        placeholder=""
                                        defaultValue={
                                            ctdt?.quiTrinhDaoTao || ""
                                        }
                                        disabled={!isEditing7}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                quiTrinhDaoTao: e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    <div>
                                        {isEditing7 === false ? (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleEditButton7}
                                            >
                                                {t("CHANGE")}
                                            </a>
                                        ) : (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleSaveButton7}
                                            >
                                                {t("SAVE")}
                                            </a>
                                        )}
                                    </div>
                                </Col>
                            </Row>

                            <Row>
                                <Col lg={3}>
                                    <Label className="item-label">
                                        <b>{t("GRADUATION_CONDITIONS")}</b>
                                    </Label>
                                </Col>
                                <Col lg={7}>
                                    <Input
                                        style={{height: "250px", marginTop: "3px"}}
                                        type="textarea" 
                                        name="GRADUATION_CONDITIONS"
                                        id="GRADUATION_CONDITIONS"
                                        placeholder=""
                                        defaultValue={
                                            ctdt?.dieuKienTotNghiep || ""
                                        }
                                        disabled={!isEditing8}
                                        onChange={(e) =>
                                            setCtdt({
                                                ...ctdt,
                                                dieuKienTotNghiep:
                                                    e.target.value,
                                            })
                                        }
                                    />
                                </Col>
                                <Col lg={2}>
                                    <div>
                                        {isEditing8 === false ? (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleEditButton8}
                                            >
                                                {t("CHANGE")}
                                            </a>
                                        ) : (
                                            <a
                                                className="typeLink"
                                                type="button"
                                                onClick={handleSaveButton8}
                                            >
                                                {t("SAVE")}
                                            </a>
                                        )}
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row className="ManageProgramEducation_background ManageProgramEducation__contents">
                        <Col>
                            <h4 className="item-label">
                                {t("RELATED_MAJOR").toUpperCase()}
                            </h4>
                            {danhSachNganhDaoTao?.total ? (
                                <Table className="mt-4">
                                    <thead>
                                        <tr>
                                            <th>STT</th>
                                            <th>{t("MAJORS_CODE")}</th>
                                            <th>{t("MAJORS_NAME")}</th>
                                        </tr>
                                    </thead>
                                    {danhSachNganhDaoTao.contents.map(
                                        (item, index) => (
                                            <tbody key={index}>
                                                <tr>
                                                    <th scope="row">
                                                        {index + 1}
                                                    </th>
                                                    <td>
                                                        {item.maNganhDaoTao}
                                                    </td>
                                                    <td>
                                                        <p>{item.ten}</p>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        )
                                    )}
                                </Table>
                            ) : (
                                i18n.t("EMPTY")
                            )}
                        </Col>
                    </Row>
                </div>
            )}
        </>
    );
};

export default AdminQLCTDT;
