import axiosClient from "api/common/axiosClient";

const adminQLCTDTApi = {
  getCTDT: (id) => {
    const url = "chuong-trinh-dao-tao/" + id;
    return axiosClient.get(url);
  },
  getCTDTwithConditions: (conditions) => {
    const url = `chuong-trinh-dao-tao?page=${conditions.page}&limit=${conditions.limit}&updatedAt=${conditions.updatedAt}`;
    if (conditions.search !== "") {
      url += `search=&${conditions.search}`;
    }
    if (conditions.maCTDT !== "") {
      url += `maCTDT=&${conditions.maCTDT}`;
    }
    return axiosClient.get(url);
  },
  updateCTDT: (id, ctdt) => {
    const url = "chuong-trinh-dao-tao/" + id;
    return axiosClient.put(url, ctdt);
  },
  getNganhDaoTaoTuCTDT: (data) => {
    const url = `nganh-dao-tao?chuongTrinhDaoTao=${data}`;
    return axiosClient.get(url);
  },
};
export default adminQLCTDTApi;
